'use strict';

// @macbook
var macbook = Vue.extend({
  template: '<div class="macbook macbook-close">\n               <div class="macbook-lid">\n                 <div class="macbook-screen">\n                    <div class="macbook-content">\n                      <div class="macbook-code"></div>\n                      <div class="macbook-code"></div>\n                      <div class="macbook-code"></div>\n                      <div class="macbook-code"></div>\n                      <div class="macbook-code"></div>\n                      <div class="macbook-code"></div>\n                      <div class="macbook-code"></div>\n                      <div class="macbook-code"></div>\n                    </div>\n                  </div>\n                </div>\n              <div class="macbook-lid-close"></div>\n              <div class="macbook-base"></div>'
});

Vue.component('my-macbook', macbook);
new Vue({
  el: '#computerAndCoffee'
});

// Open macbook
// Check if its in view
function isScrolledIntoView(elem) {
  var el = $('#computerAndCoffee');

  var docViewTop = $(window).scrollTop();
  var docViewBottom = docViewTop + $(window).height();

  var elemTop = el.offset().top;
  var elemBottom = elemTop + el.height();

  return elemBottom <= docViewBottom && elemTop >= docViewTop;
}

var i = window.setInterval(function () {
  if (isScrolledIntoView()) window.setTimeout(openMacbook, 300);
}, 1000);

function openMacbook() {
  document.querySelector('.macbook').classList.remove('macbook-close');
  $('.macbook-lid-close').hide();
  window.clearInterval(i);
}